<template>
  <div id="indexMap" class="index-map" :center="center">
    <el-amap vid="amap" :plugin="plugin" :zoom="14" :events="events">
      <el-amap-marker v-for="marker in markers" key="marker" :position="marker.position" :visible="marker.visible" :draggable="marker.draggable"></el-amap-marker>
    </el-amap>
  </div>
</template>
<style>
  .index-map{
    height: 400px;
  }
</style>

<script>
  import Vue from 'vue';
  import AMap from 'vue-amap';
  Vue.use(AMap);
  AMap.initAMapApiLoader({
    key: '54a7c0a2a9f311c1811e63db69e4f7ca',
    plugin: ['Geolocation']
  });
  export default {
    data() {
      let self = this;

      return {
        vid: 'amap',
        center: [],
        markers: [
          {
            position: [11,22],
            visible: true,
            draggable: false
          }
        ],
        events: {

        },
        plugin: [{
          pName: 'Geolocation',
          enableHighAccuracy: true,//是否使用高精度定位，默认:true
          timeout: 10000,          //超过10秒后停止定位，默认：无穷大
          maximumAge: 0,           //定位结果缓存0毫秒，默认：0
          convert: true,           //自动偏移坐标，偏移后的坐标为高德坐标，默认：true
          showButton: true,        //显示定位按钮，默认：true
          buttonPosition: 'LB',    //定位按钮停靠位置，默认：'LB'，左下角
          showMarker: true,        //定位成功后在定位到的位置显示点标记，默认：true
          showCircle: true,        //定位成功后用圆圈表示定位精度范围，默认：true
          panToLocation: true,     //定位成功后将定位到的位置作为地图中心点，默认：true
          zoomToAccuracy:true,     //定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
          events: {
            init(instance) {
              instance.getCurrentPosition((status, result) => {
                alert(result.message);
                self.center = [result.position.lng, result.position.lat];
                self.markers.position = [result.position.lng, result.position.lat];
              });
            }
          }
        }]
      };
    }
  };
</script>



