<template>
  <div>
    <yd-navbar title="叫我修吧" :bgcolor="bgcolor" :color="color">
      <router-link to="#" slot="left">
        <yd-icon name="ucenter" size="25px" color="#fff"></yd-icon>
      </router-link>
      <router-link to="#" slot="right">
        <yd-icon name="shopcart" size="25px" color="#fff"></yd-icon>
      </router-link>
    </yd-navbar>
    <category></category>
    <div class="index-main">
      <notice></notice>
      <index-map></index-map>
    </div>
  </div>
</template>

<style>
  .index-main{
    position: relative;
  }
</style>

<script>
import Vue from 'vue';
import {Icons} from 'vue-ydui/dist/lib.rem/icons';
import {NavBar, NavBarBackIcon, NavBarNextIcon} from 'vue-ydui/dist/lib.rem/navbar';
import Category from '@/components/index-category';
import Notice from '@/components/notice-list'
import IndexMap from '@/components/index-map';

Vue.component(Icons.name, Icons);
Vue.component(NavBar.name, NavBar);
Vue.component(NavBarBackIcon.name, NavBarBackIcon);
Vue.component(NavBarNextIcon.name, NavBarNextIcon);
Vue.component('category', Category);
Vue.component('notice', Notice);
Vue.component('index-map', IndexMap);

export default {
  name: 'index',
  data () {
    return {
      bgcolor: '#313131',
      color: '#fff'
    }
  }
}
</script>



